.toast {
  visibility: hidden;
  min-width: 250px;
  margin-left: -125px;
  background-color: #fff;
  color: #000;
  text-align: center;
  border-radius: 4px;
  padding: 14px;
  position: fixed;
  z-index: 1;
  left: 50%;
  bottom: 30px;
  box-shadow: 0px 8px 18px rgba(0, 0, 0, 0.2), 0 1px 4px 0 rgba(0,0,0,0.1), 0 1px 2px 0 rgba(0,0,0,0.08);
}

.toast.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@-webkit-keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}
main#home {
  margin-top: 80px;
  margin-bottom: 80px;
}
.shelf {
  margin-bottom: 36px;
}

.shelf h3 {
  color: #232323;
  font-size: 24px;
  padding-bottom: 18px;
  margin-bottom: 18px;
  font-weight: 500;
  border-bottom: 1px solid #2223;
}
.skeleton {
  display: -webkit-flex;
  display: flex;
  margin-left: -15px;
  margin-right: -15px;
}

.skeleton li {
  margin: 15px;
}

.skeleton li span {
  margin: 4px 0;
}
.shelf .book-list {
  display: -webkit-flex;
  display: flex;
  margin-left: -15px;
  margin-right: -15px;
}

.book-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.shelf .book {
  width: calc(20% - 30px);
  margin: 15px;
}

.shelf .book__cover {
  position: relative;
  height: 208px;
  box-shadow: 0px 8px 18px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}

@media only screen and (max-width: 600px) {
  .shelf .book {
    max-width: 127px;
  }
}

.shelf .book__cover img {
  border-radius: 8px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.shelf .book__cover a:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: rgba(255,255,255,0.7);
  -webkit-transition: none;
  transition: none;
}

.shelf .book__cover a:hover:after {
  width: 120%;
  background-color: rgba(255,255,255,0);
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.shelf .book__maturity-rating {
  position: absolute;
  left: 0;
  top: 15px;
  background-color: orange;
  color: #fff;
  padding: 2px 8px;
}

.book .book__options {
  position: absolute;
  right: 13px;
  bottom: -14px;
  width: 34px;
  height: 34px;
  border-radius: 100%;
  border: 1px solid rgba(53, 53, 53, 0.15);
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.12);
  background: url(/davidsilvasp/my-reads/static/media/more-vertical.931168a7.svg) center no-repeat #F1F1F1;
  background-size: 65%;
  overflow: hidden;
}

.book .book__options:hover {
  background-color: #d8d8d8;
}

.book .book__options select {
  cursor: pointer;
  opacity: 0;
  width: 100%;
  height: 38px;
}

.book .book__authors small {
  background: #02b3e4;
  padding: 2px 8px;
  border-radius: 4px;
  color: #fff;
  margin: 16px 0 9px;
  display: inline-block;
}
main#search {
  margin-top: 36px;
  margin-bottom: 80px;
}

.search-input {
  max-width: 878px;
  margin: 64px auto 24px;
  border-radius: 8px;
  padding: 0 24px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background-color: #ffffff;
  box-shadow: 0 1px 4px 0 rgba(0,0,0,0.1), 0 1px 2px 0 rgba(0,0,0,0.08);
}

.search-input input {
  display: inline-block;
  padding: 18px 16px;
  -webkit-flex: 1 1;
          flex: 1 1;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  border: 0;
  outline: 0;
}

.result-message {
  text-align: center;
  margin: 36px 0;
}

.result-message span {
  padding: 2px 10px;
  border-radius: 4px;
  border: 1px solid currentColor;
}
